//  

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { notification } from 'antd';

interface User {
  username: string;
  email: string;
  isAuthenticated: boolean;
  isStaff: boolean;
}

interface DjangoContext {
  user: User;
  apiBaseUrl: string;
  csrfToken: string;
}

interface AuthContextValue {
  user: User | null;
  djangoContext: DjangoContext | null;
  isAuthenticated: boolean;
  isStaff: boolean;
  loading: boolean;
  login: (username: string, password: string) => Promise<void>;
  logout: () => Promise<void>;
  refreshUser: () => Promise<void>;
}

const AuthContext = createContext<AuthContextValue | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
  context?: DjangoContext | null;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children, context }) => {
  const [user, setUser] = useState<User | null>(null);
  const [djangoContext, setDjangoContext] = useState<DjangoContext | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Django    
    if (context) {
      setDjangoContext(context);
      if (context.user && context.user.isAuthenticated) {
        setUser(context.user);
      }
    }
    setLoading(false);
  }, [context]);

  const login = async (username: string, password: string): Promise<void> => {
    try {
      // Django  API 
      const response = await fetch('/accounts/login/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken': djangoContext?.csrfToken || ''
        },
        body: JSON.stringify({ username, password }),
        credentials: 'include'
      });

      if (response.ok) {
        const userData = await response.json();
        setUser({
          username: userData.username,
          email: userData.email,
          isAuthenticated: true,
          isStaff: userData.is_staff || false
        });
        notification.success({
          message: ' ',
          description: `, ${userData.username}!`
        });
      } else {
        throw new Error(' ');
      }
    } catch (error) {
      notification.error({
        message: ' ',
        description: '   .'
      });
      throw error;
    }
  };

  const logout = async (): Promise<void> => {
    try {
      // Django  API 
      const response = await fetch('/accounts/logout/', {
        method: 'POST',
        headers: {
          'X-CSRFToken': djangoContext?.csrfToken || ''
        },
        credentials: 'include'
      });

      if (response.ok) {
        setUser(null);
        notification.success({
          message: ' ',
          description: ' .'
        });
        //   
        window.location.href = '/accounts/login/';
      }
    } catch (error) {
      notification.error({
        message: ' ',
        description: ' .'
      });
      throw error;
    }
  };

  const refreshUser = async (): Promise<void> => {
    try {
      setLoading(true);
      //    API 
      const response = await fetch('/api/auth/user/', {
        credentials: 'include'
      });

      if (response.ok) {
        const userData = await response.json();
        setUser({
          username: userData.username,
          email: userData.email,
          isAuthenticated: true,
          isStaff: userData.is_staff || false
        });
      } else {
        setUser(null);
      }
    } catch (error) {
      console.error('   :', error);
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const value: AuthContextValue = {
    user,
    djangoContext,
    isAuthenticated: user?.isAuthenticated || false,
    isStaff: user?.isStaff || false,
    loading,
    login,
    logout,
    refreshUser
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

//  
export const useAuth = (): AuthContextValue => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

//   HOC
export const withAuth = <P extends object>(
  Component: React.ComponentType<P>,
  requireStaff: boolean = false
): React.FC<P> => {
  return (props: P) => {
    const { isAuthenticated, isStaff, loading } = useAuth();

    if (loading) {
      return <div> ...</div>;
    }

    if (!isAuthenticated) {
      window.location.href = '/accounts/login/';
      return null;
    }

    if (requireStaff && !isStaff) {
      return (
        <div style={{ padding: '50px', textAlign: 'center' }}>
          <h2>  </h2>
          <p>     .</p>
        </div>
      );
    }

    return <Component {...props} />;
  };
};