import React, { Suspense, lazy, useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ConfigProvider, Spin, notification } from 'antd';
import koKR from 'antd/locale/ko_KR';
import './App.css';
import MainLayout from './components/MainLayout';
import { ErrorBoundary } from './components/common/ErrorBoundary';
import { AuthProvider } from './contexts/AuthContext';
import { LoadingFallback } from './components/common/LoadingFallback';

// Lazy loading  
const QuoteManagement = lazy(() => import('./components/QuoteManagement'));
const Dashboard = lazy(() => import('./components/Dashboard'));
const CompanyManagement = lazy(() => import('./components/CompanyManagement'));
const Statistics = lazy(() => import('./components/Statistics'));
const MemberList = lazy(() => import('./components/MemberList'));
const CompanyList = lazy(() => import('./components/CompanyList'));
const Report = lazy(() => import('./components/Report'));

//  
const globalConfig = {
  theme: {
    token: {
      colorPrimary: '#667eea',
      borderRadius: 6,
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif',
    },
  },
};

// Django  
interface DjangoContext {
  user: {
    username: string;
    email: string;
    isAuthenticated: boolean;
    isStaff: boolean;
  };
  apiBaseUrl: string;
  csrfToken: string;
}

function App() {
  const [djangoContext, setDjangoContext] = useState<DjangoContext | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Django  
    const context = (window as any).__DJANGO_CONTEXT__;
    if (context) {
      setDjangoContext(context);
      console.log('Django  :', context);
    } else {
      console.warn('Django    .');
    }
    setLoading(false);

    //   
    const handleError = (event: ErrorEvent) => {
      console.error(' :', event.error);
      notification.error({
        message: ' ',
        description: '   .  .',
        placement: 'topRight',
      });
    };

    window.addEventListener('error', handleError);
    return () => window.removeEventListener('error', handleError);
  }, []);

  if (loading) {
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        background: '#f0f2f5'
      }}>
        <Spin size="large" tip="  ..." />
      </div>
    );
  }

  return (
    <ConfigProvider locale={koKR} theme={globalConfig.theme}>
      <ErrorBoundary>
        <AuthProvider context={djangoContext}>
          <Router>
            <MainLayout>
              <Suspense fallback={<LoadingFallback />}>
                <Routes>
                  {/*     */}
                  <Route path="/" element={<Navigate to="/order/list" replace />} />

                  {/*  */}
                  <Route path="/dashboard" element={<Dashboard />} />

                  {/*    */}
                  <Route path="/order">
                    <Route path="list" element={<QuoteManagement />} />
                    <Route path="company" element={<CompanyManagement />} />
                    <Route path="statistics" element={<Statistics />} />
                    <Route index element={<Navigate to="list" replace />} />
                  </Route>

                  {/*    */}
                  <Route path="/member">
                    <Route path="list" element={<MemberList />} />
                    <Route path="company" element={<CompanyList />} />
                    <Route index element={<Navigate to="list" replace />} />
                  </Route>

                  {/*  */}
                  <Route path="/report" element={<Report />} />

                  {/*     ( ) */}
                  <Route path="/pplist" element={<Navigate to="/order/list" replace />} />
                  <Route path="/order/pplist" element={<Navigate to="/order/list" replace />} />

                  {/* 404  */}
                  <Route
                    path="*"
                    element={
                      <div style={{
                        padding: '100px 24px',
                        textAlign: 'center',
                        minHeight: '60vh',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center'
                      }}>
                        <h1 style={{ fontSize: '72px', marginBottom: '16px' }}>404</h1>
                        <h2 style={{ marginBottom: '24px' }}>   </h2>
                        <p style={{ marginBottom: '32px', color: '#666' }}>
                              .
                        </p>
                        <button
                          onClick={() => window.location.href = '/order/list'}
                          style={{
                            padding: '12px 24px',
                            background: '#667eea',
                            color: 'white',
                            border: 'none',
                            borderRadius: '6px',
                            fontSize: '16px',
                            cursor: 'pointer'
                          }}
                        >
                            
                        </button>
                      </div>
                    }
                  />
                </Routes>
              </Suspense>
            </MainLayout>
          </Router>
        </AuthProvider>
      </ErrorBoundary>
    </ConfigProvider>
  );
}

export default App;